import { Component } from 'inferno';
import register from './registerServiceWorker';
import './App.css';
import EntriesContainer from './EntriesContainer';

register();
class App extends Component {
  render() {
    return (
      <div className="App">
        <h1>Tracker</h1>
        <EntriesContainer></EntriesContainer>
      </div>
    );
  }
}

export default App;



