import { Component } from 'inferno';

export default class EntryForm extends Component {
  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label for="date">Date</label>
        <input id="date" name="date" type="date" value={this.state.date} onInput={this.onChange} />
        <label for="weight">Weight</label>
        <input id="weight" type="number" name="weight" value={this.state.weight} onInput={this.onChange} step="any" min="0" />
        <div class="button-row">
          <button type="button" disabled={this.deleteDisabled()} onClick={this.handleDelete}>Delete</button><button disabled={this.disabled()}>{this.label()}</button>
        </div>
      </form>
    );
  }

  constructor(props) {
    super(props);
    this.state = this.getInitialState();
  }

  getInitialState() {
    const today = new Date();
    return {
      weight: undefined,
      date: `${today.getFullYear()}-${('0' + (today.getMonth() + 1)).slice(-2)}-${today.getDate()}`
    };
  }

  componentWillReceiveProps({ entry }) {
    if (entry !== this.props.entry && entry) {
      this.setState({
        weight: entry.weight,
        date: entry.date
      });
    }
  }

  onChange = ({ target: { name, value } }) => {
    this.setState({ [name]: value });
  }

  disabled() {
    return !this.state.date || !this.state.weight;
  }

  deleteDisabled() {
    return !(this.state.date && localStorage.getItem(this.state.date));
  }

  label() {
    return this.deleteDisabled() ? 'Add' : 'Update';
  }

  handleSubmit = (event) => {
    event.preventDefault();
    localStorage.setItem(this.state.date, this.state.weight);
    this.props.handleUpdate && this.props.handleUpdate();
  }

  handleDelete = () => {
    this.state.date && localStorage.removeItem(this.state.date);
    this.props.handleUpdate && this.props.handleUpdate();
    this.setState(this.getInitialState());
  }
}


