import { to1DP } from './Utils';

const EntriesTable = ({ entries, onRowSelect }) =>
  entries && entries.length ?
    (<table>
      <thead>
        <tr><th>Date</th><th>Weight</th></tr>
      </thead>
      <tbody>
        {entries.map((entry) => (<tr onClick={() => onRowSelect(entry)}><td>{new Date(entry.date).toLocaleDateString('en-gb')}</td><td>{to1DP(entry.weight)}</td></tr>))}
      </tbody>
    </table>) :
    (<div>No entries</div>);

export default EntriesTable;


