import { to1DP } from './Utils';

const getAvereage = (entries) => {
  const weights = Array.isArray(entries) ? entries.map(entry => entry.weight) : [],
    total = weights.reduce((a, b) => parseFloat(a) + parseFloat(b), 0);

  return to1DP(weights.length ? total / weights.length : 0);
}

const Average = ({ entries }) => (
  <div class="average">
    <span>Average</span><span>{getAvereage(entries)}</span>
  </div>);

export default Average;


