import { Component } from 'inferno';
import EntriesTable from './EntriesTable';
import EntryForm from './EntryForm';
import Average from './Average';

export default class EntriesContainer extends Component {

  constructor(props) {
    super(props)
    this.state = {
      entries: this.getEntriesFromStorage(),
      currentEntry: null
    };

    window.addEventListener('storage', () => this.refreshTable());
  }

  getEntriesFromStorage() {
    return Object.keys(localStorage).map((date) => ({
      date,
      weight: localStorage.getItem(date)
    })).sort((a, b) => a.date.localeCompare(b));
  }

  refreshTable = () => {
    this.setState({ entries: this.getEntriesFromStorage() });
  }

  onRowSelect = (entry) => {
    this.setState({ currentEntry: entry });
  }

  render() {
    return (
      <div class="main">
        <EntryForm handleUpdate={this.refreshTable} entry={this.state.currentEntry}></EntryForm>
        <EntriesTable entries={this.state.entries} onRowSelect={this.onRowSelect}></EntriesTable>
        <Average entries={this.state.entries}></Average>
      </div>
    );
  }
}


